.bg-contact {
  background-image: url(../images/bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: #e9f4dc;
  background-blend-mode: multiply;
  padding: 30px; }

.contact-content {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: calc(100% -60px);
  color: #fff; }

.contact-content hr {
  width: 50px;
