  .contact-left-info {
    width: 50%; }
  .contact-left-map {
    width: 50%; } }

@media (min-width: 992px) {
  .contact-content {
    margin: 0 10%;
    height: fit-content; } }

@media (min-width: 1200px) {
  .contact-content {
    padding: 50px 0;
    flex-direction: row;
    margin: 0 10%;
    height: fit-content; }
  .contact-left {
    width: 70%; }
  .contact-right {
    width: 30%;
    margin-top: 0; } }

.footer {
  overflow-x: hidden;
  padding: 3rem 0 3rem 0;
  background-color: #111;
  color: #bdbdbd; }
  .footer h6 {
    text-transform: uppercase;
    font-weight: 700;
    color: #bdbdbd;
    height: 30px; }
  .footer .logo-footer {
