    clear: both; }
  .footer ul {
    margin: 0;
    padding: 0;
    display: block; }
    .footer ul li {
      list-style: none; }
      .footer ul li a {
        color: #bdbdbd;
        text-decoration: none;
        transition: all 0.4s; }
        .footer ul li a:hover {
          color: #8ac53f; }

.copy {
